import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';
import { route } from 'preact-router';

import NewsDetail from '../../components/news/detail';
import NewsComments from '../../components/news/comments';
import NewsAddComment from '../../components/news/commentAdd';

import mu from '../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(false);
const MARGIN_TOP_BACK = mu.marginTopBack(false);
const MARGIN_BOTTOM = mu.marginBottom();

const initialState = {
	isLoading: false,
    showAddComment: false,
};

@observer
class News extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
        this.inputRef = {};
    }

	async loadNews(props = this.props) {
		this.setState({ isLoading: true });
		const { newsStore } = this.props.stores;
		await newsStore.load(props.id);
		this.setState({ isLoading: false });
	}

    likeNews = async () => {
		const { newsStore } = this.props.stores;
		const { news } = newsStore;
		const response = await newsStore.likeNews({ id: news.id });

        if (response && response.status === 200) {
			// this.loadNewsDetail({ news });
		}
	}

	toggleAddComment = (e) => {
		const newsId = e.target.parentElement.dataset.id || e.target.dataset.id;
		const { showAddComment } = this.state;
		this.setState({
			showAddComment: !showAddComment,
		}, () => {
			if (this.inputRef[newsId]) {
				this.inputRef[newsId].focus();
			}
		});
	}

	componentDidMount() {
		this.loadNews();
	}

    back = () => {
        route(`/news`);
    }

	render() {
        const { showAddComment } = this.state;
		const { newsStore, userStore } = this.props.stores;
        const { isAdmin } = userStore;
		const { news } = newsStore;

		const { user } = userStore;
        const { likes = [], comments = [] } = news;
		const hasLikedNews = news.likes && news.likes.findIndex(e => e.user === user.id) > -1;

		return (
			<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>
                <div class='d-flex justify-content-between mb-2'>
                    <div>
                        <button class='btn btn-link pl-0 fixed-top text-primary'
                            style={`top: ${MARGIN_TOP_BACK}; left: 5px; font-size: 35px; width: 45px;`}
                            onClick={this.back}>
                            <i class='fas fa-arrow-circle-left' />
                        </button>
                    </div>
                    <div>
                        <h5 class='text-muted'>
                            <Text id='news.heading'>Siste nytt</Text>
                        </h5>
                    </div>
                    <div>
                        {isAdmin && <a class='btn btn-sm btn-primary float-right ml-2'
							href={`/news/edit/${news.id}`}
						>
							<i class='fas fa-edit' /> <Text id='news.edit'>Endre</Text>
						</a>}
                        {isAdmin && <a class='btn btn-sm btn-success float-right ml-2'
							href={`/news/edit/new`}
						>
							<i class='fas fa-plus' /> <Text id='news.create'>Ny</Text>
						</a>}
                    </div>
                </div>

				<div class='row'>
					<div class='col-12'>
                        <NewsDetail stores={this.props.stores} news={news} />
					</div>


                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-3'>
						{likes && likes.length > 0 && <small class='bg-light shadow-sm rounded-lg py-0 px-1' style='bottom: 20px; right: 15px;'>
							<i class='fas fa-thumbs-up text-primary' /> {likes.length}
						</small>}
						{comments && comments.length > 0 && <small class='float-right bg-light shadow-sm rounded-lg py-0 px-1' style='bottom: 20px; right: 15px;'>
							<i class='fas fa-comment text-primary' /> {comments.length}
						</small>}
					</div>

					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-2 mb-3'>
						<div class='row'>
							<div class='col-6 text-center'>
								<button type='button' class={`btn btn-outline-${hasLikedNews ? 'primary' : 'secondary'} btn-lg btn-block ${hasLikedNews ? 'text-primary' : ''}`}
									onClick={this.likeNews}
									// disabled={hasLikedNews}
								>
									<i class={`${hasLikedNews ? 'text-primary fas' : 'far'} fa-thumbs-up`} /> Like
								</button>
							</div>
							<div class='col-6 text-center'>
								<button type='button' class='btn btn-outline-secondary btn-lg btn-block'
									data-id={news.id}
									onClick={this.toggleAddComment}
								>
									<i class='fas fa-comment' /> Comment
								</button>
							</div>
						</div>
					</div>


					{showAddComment && <NewsAddComment
						stores={this.props.stores}
						news={news}
						// callback={this.loadNewsDetail}
						inputRef={c => this.inputRef[news.id] = c}
					/>}
					<NewsComments
						stores={this.props.stores}
						news={news}
                        cols='12'
						// callback={this.loadNewsDetail}
					/>

				</div>

				<div class='row'>
					<div class='col-12'>
                        <a href='/news' class='btn btn-secondary'>
                            <i class='fas fa-arrow-left mr-2' />
                            <Text id='news.back'>Tilbake</Text>
                        </a>
					</div>
				</div>
			</div>
		);
	}
}

export default News;
